
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'
import CountDownthirtymin from './plugins/CountDownthirtymin.vue'

export default {

directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
Confirm,
XSwitch,
Datetime,
Blur,
Tab,
TabItem,
XAddress,
CountDownthirtymin,
Checklist
},
filters: {
feeFormat (value) {
if (!value) {
return '包邮'
}
}
},
computed: {
leftOptions () {
return {
showBack: true,
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
}
},
mounted () {
this.giftId = this.$route.query.giftId
this.failureShowToast = false
this.changeSuccessShow = false
this.getGift()
this.getUserInfo()
},
data () {
return {
title: 'Redeem',
timeRange: 30,
giftId: '',
giftIcon: '',
giftTitle: '',
score: 0,
giftScore: 0,
ownPic: '../assets/dialog/01.jpg',
url: '../assets/dialog/01.jpg',
showConfirmBox: false,
computeDaysValue: '',
index: 0,
mobile: '',
iconType: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
failureText: '',
defaultMethod: '1',
payMethodList: [
{key: '1', value: '支付宝支付', inlineDesc: '使用支付宝账号支付'},
{key: '2', value: '微信支付', inlineDesc: '使用微信账号支付'}
]
}
},
methods: {
timeOutFun () {
},
redeemClick () {
this.showConfirmBox = true
},
onPicIndexChange () {

},
goRedeemDetail () {
this.$router.push('setting')
},
goStruction () {
this.$router.push('setting')
},
goQuestion () {
this.showConfirmBox = true
},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
onCancel () {

},

onConfirm () {
this.$router.push('question')
},
onShow () {

},
onHide () {

},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
getGift () {
this.$http.post(this.$apis.getGiftById, {giftId: this.giftId}).then(res => {
if (res.code === 200) {
this.giftTitle = res.data.giftTitle
this.expressFee = res.data.expressFee
this.giftScore = res.data.giftScore
this.originalPrice = res.data.originalPrice
this.giftDetail = res.data.giftDetail
this.giftIcon = res.data.giftIcon
this.$vux.loading.hide()
} else {
this.$vux.loading.hide()
}
}).catch(function (error) {
console.log('Error: ' + error)
this.$vux.loading.hide()
})
},
getUserInfo () {
let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.userInfo, {userId: userId})
.then((res) => {
if (res.code === 200) {
this.score = res.data.score
this.username = res.data.username
}
})
.catch(function (error) {
console.log(error)
})
}
}

}



